
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
/*  color: #2c3e50;
  margin-top: 60px;*/
}
*{
  margin:0;
  padding:0;
}
#topInfo{
    height:50px;
    background-color: #ffffff;
    position: relative;
    -webkit-box-shadow: 0px 3px 5px 0px rgba(0,49,98,0.08);
            box-shadow: 0px 3px 5px 0px rgba(0,49,98,0.08);
}
#topInfo .logoInfo{
    position: absolute;
    right: 0px;
    top: 0;
    font-size: 0;
    height: 100%;
}
#main .leftNav{
    float: left;
    width: 60px;
    height:500px;
    background: #F2F2F2;
    min-height: 100%;
    position: relative;
}
#main .leftNav .functionBtn{
    width: 100%;
    height: 60px;
    position: relative;
    cursor: pointer;
    text-align: center;
    line-height: 60px;
}
#mamin .rightContent{
    margin-left: 60px;
    background: #f1f2f4;
    min-height: 500px;
    position: relative;
}
.active{
  background-color:#E3E3E3;
}
